@media screen{
    .topNavBar_wrapper{
        padding: 23px 16px;
        width: 100%;
        position: fixed;
        z-index: 999;
        top: 0;
        left: 0;
        display: flex;
        justify-content: space-between;
        align-items: center;
        color: #b7b7b7;
        transition: all 0.2s;
    }
        .topNavBar_wrapper .logo{
            font-size: 24px;
            font-family: 'Arial Black';
        }
            .topNavBar_wrapper .logo .resumeHint{
                margin-right: 8px;
                color: #e6686a;
            }
            .topNavBar_wrapper .logo .nameHint{
                color: #9a9da2;
            }
        .topNavBar_wrapper .nav{
            display: flex;
        }
            .topNavBar_wrapper .nav li{
                margin: 0 8px;
                padding: 0 12px;
                position: relative;
                cursor: pointer;
            }
                .topNavBar_wrapper .nav li a{
                    display: block;
                    font-size: 12px;
                    line-height: 22px;
                    height: 22px;
                    font-weight: bold;
                    position: relative;
                }
    ul.nav > li:hover > a::after{
        content: '';
        display: block;
        width: 100%;
        height: 3px;
        background: #e6686a;
        position: absolute;
        top: calc(100% + 3px);
        left: 0;
        animation: slide .3s ease;
    }
    @keyframes slide{
        0%{width: 0;}
        100%{width: 100%;}
    }
    
    .topNavBar_active{
        transition: all 0.5s;
        background: #fff;
        color: #8c8a8a;
        padding: 13px 16px;
        box-shadow: 0 0 10px rgba(0,0,0,0.25);
    }
}



