@media print{
    .skillChart_wrapper{
        width: 100%;
        margin-top: 10px;
        h2{
            padding-top: 17px;
            text-align: center;
            margin-bottom: 15px;
            font-weight: 600;
        }
        main{
            // border: 1px solid red;
            padding: 10px;
            position: relative;
            background: #ffffff;
            border-radius: 3px;
            overflow: hidden;
            .skillDescription{
                // border: 1px solid red;
                padding: 10px;
                margin-right: 440px;
                .skill_row{
                    margin-bottom: 20px;
                    h3{
                        margin-bottom: 12px;
                    }
                    p{
                        font-size: 16px;
                        line-height: 20px;
                        margin-left: 34px;
                        font-family: '楷体';
                    }
                }    
            }
            .skillChart{
                transform: scale(0.9);
                position: absolute;
                top: 30px;
                right: -50px;
                // border: 1px solid red;
                width: 500px;
                .highcharts-title{
                    display: none;
                }
                .highcharts-legend{
                    transform: translate(340px,20px); 
                }
    
    
            }
        }
    }
}